* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  user-select: none;
}

body {
  background-color: #140033;
}

nav {
  background-color: #18003bd2;
  padding: 10px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  margin-bottom: 60px;
}

nav img {
  width: 100px;
}

nav a {
  text-decoration: none;
  padding: 10px;
  margin: 2px;
  font-size: x-large;
}

nav a:hover {
  color: #9052ecd2;
}

@media (max-width: 400px) {
  nav img {
    margin-bottom: 20px;
  }
  nav a {
    font-size: large;
  }
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 20px;
  flex-wrap: wrap;
  margin-bottom: 70px;
}

.container img {
  width: 40%;
}

.container .text {
  font-size: 20px;
  width: 40%;
}

.container .text h1,
p {
  color: aliceblue;
  padding: 7px;
}

.container .text h4 {
  display: inline-block;
  color: #9052ecd2;
}

.container .text .a {
  margin-top: 20px;
}

.container .text .a a {
  text-decoration: none;
}

.container .text .a p {
  padding: 10px;
  font-size: larger;
  color: aliceblue;
  background-image: url(../src/3D/Ellipse\ 5.png);
  background-size: contain;
  background-repeat: no-repeat;
  text-decoration: none;

  border-right: 2px solid blue;
  overflow: hidden;
  white-space: nowrap;
  animation: control-width 4s steps(20) 3s both, blink 0.5s infinite;
}

@keyframes control-width {
  from {
    width: 0px;
  }
  to {
    width: 150px;
  }
}
@keyframes blink {
  from {
    border-right-color: blue;
  }
  to {
    border-right-color: transparent;
  }
}

.container .text .a p:hover {
  cursor: pointer;
  color: #9052ecd2;
}

@media (max-width: 700px) {
  .container {
    flex-direction: column;
  }
  .container .text {
    margin-top: 20px;
    font-size: 14px;
  }
}

.work {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
}

.work img {
  width: 300px;
  margin: 20px;
  padding: 7px;
}

.work img:first-child {
  box-shadow: 2px 2px 10px 10px blue;
}

.proj {
  margin-bottom: 40px;
}

.proj h2 {
  text-align: center;
  font-size: 50px;
  text-shadow: 4px 4px 4px blue;
  margin-bottom: 20px;
}

.proj .item {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.proj .item img {
  width: 700px;
  padding: 8px;
}

.proj .item .img2 {
  margin-top: 100px;
}

.proj .item img:hover {
  scale: 1.1;
  transition: 1s;
  cursor: pointer;
}

.wi {
  width: 70%;
  overflow: hidden;
}

.marquee {
  white-space: nowrap;
  box-sizing: border-box;
}

.marquee::after {
  content: "";
  display: inline-block;
  width: 100%;
}

.marquee {
  animation: marquee 44s linear infinite;
  display: flex;
  margin: 10px;
}

.proj .item .con {
  width: 240px;
  height: 200px;
  background-color: #111111;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border-radius: 4px;
  padding: 10px;
  margin: 10px;
}

.proj .item .con img {
  width: 200px;
  height: 200px;
  padding: 20px;
  border-radius: 6px 6px 0 0;
}

.proj .item .con a {
  color: aqua;
  text-decoration: none;
  overflow: hidden;
}

@keyframes marquee {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(-100%);
  }
}

@media (max-width: 700px) {
  .proj .item img {
    width: 400px;
  }
}

.container {
  margin-bottom: 40px;
}

.container h2 {
  color: #4300a8;
  font-size: 50px;
}

.container .con {
  text-align: center;
  display: flex;
}

.container .con .con1 {
  margin: 10px;
}

.container .con .con1 img {
  width: 400px;
  height: 300px;
  margin: 10px;
}

.container .con .con1 img:hover {
  scale: 1.1;
}

@media (max-width: 700px) {
  .container .con .con1 img {
    width: 200px;
    height: 150px;
  }
}

.arr {
  width: 100px !important;
  height: 100px;
}

.contact {
  background-color: #4300a8de;
  border-radius: 240px 240px 0 0;
}

.contact h2 {
  text-align: center;
  margin: 10px;
  padding: 10px;
  font-size: xx-large;
}

.contact-Me {
  display: flex;
  justify-content: center;
}

.contact img {
  width: 60px;
}

.contact a:hover {
  scale: 1.1;
}
